body{margin:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:linear-gradient(50deg, #282f31 0%, #2d4349 100%);font-family:"Share Tech Mono"}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
.App{text-align:center;min-height:100vh}.particles{position:absolute;z-index:-2}input:focus,label:focus,button:focus{outline:none}
header{display:flex;justify-content:space-between;align-items:center;box-shadow:0 5px 10px #171818;padding:10px 2%;background-color:rgba(0,0,0,.3)}header nav{display:flex}header nav p{border:solid 2px #34aca6;border-radius:15px;background:linear-gradient(145deg, #25e4ab 0%, #34aca6 50%, #297e77 100%);color:#171818;padding:5px;box-shadow:0 1px 8px #171818;font-family:"Share Tech Mono";margin:10px;width:auto;font-size:130%;text-decoration:underline}header nav p:hover{cursor:pointer;color:#171818;box-shadow:inset 0 2px 10px #297e77;border-color:#297e77}
.logo-box .tilt{width:60px;height:60px;background:linear-gradient(145deg, #25e4ab 0%, #34aca6 50%, #297e77 100%);border:solid 2px #34aca6;border-radius:15px;box-shadow:0 1px 8px #171818}.logo-box .tilt .logo-svg{padding-top:40px;fill:#34aca6}
.form-box{width:auto;padding:0 2%}.form-box p{font-size:140%;font-weight:500;color:#000}.form-box .form-input{display:flex;justify-content:center}.form-box input{width:90%;background:linear-gradient(to bottom, #1b2424 0%, rgba(0, 0, 0, 0.2) 100%);padding:5px;padding-left:10px;background:linear-gradient(to bottom, #1b2424 0%, rgba(0, 0, 0, 0.2) 100%);color:#34aca6;border:solid 2px #2d4349;box-shadow:0 2px 8px #171818;border-radius:15px;width:70%;font-size:130%}.form-box button{border:solid 2px #34aca6;border-radius:15px;background:linear-gradient(145deg, #25e4ab 0%, #34aca6 50%, #297e77 100%);color:#171818;padding:5px;box-shadow:0 1px 8px #171818;font-family:"Share Tech Mono";width:100px;font-size:140%;margin-left:10px}.form-box button:hover{cursor:pointer;color:#171818;box-shadow:inset 0 2px 10px #297e77;border-color:#297e77}
.rank-box{width:35%;margin:40px auto;padding:10px}.rank-box .rank-text{font-size:140%;color:#34aca6}.rank-box .rank-num{font-size:200%;color:#34aca6;text-shadow:0 0 4px #297e77}
.facerecognition-box{display:flex;justify-content:center;margin-top:30px}.facerecognition-box .image-box{position:absolute}.facerecognition-box .image-box img{width:500px;height:auto}.facerecognition-box .image-box p{color:#171818}.facerecognition-box .image-box .bounding-box{position:absolute;box-shadow:0 0 10px #34aca6 inset;border:solid 2px #34aca6;display:flex;flex-wrap:wrap;justify-content:center;cursor:pointer}
.signin-box{width:300px;height:auto;margin:50px auto;background:linear-gradient(to bottom, #1b2424 0%, rgba(0, 0, 0, 0.2) 100%);font-size:120%;padding:10px;box-shadow:0 2px 8px #171818;border-radius:15px}.signin-box .form p{color:#297e77;font-size:90%;text-decoration:underline;margin:0 auto}.signin-box .form p:hover{color:#34aca6;cursor:pointer}.signin-box .form fieldset{border:solid 2px #282f31}.signin-box .form fieldset legend{color:#34aca6;font-size:170%;padding:0 10px}.signin-box .form fieldset input{width:90%;background:linear-gradient(to bottom, #1b2424 0%, rgba(0, 0, 0, 0.2) 100%);padding:5px;padding-left:10px;background:linear-gradient(to bottom, #1b2424 0%, rgba(0, 0, 0, 0.2) 100%);color:#34aca6;border:solid 2px #2d4349;box-shadow:0 2px 8px #171818;border-radius:15px;margin:10px auto;margin-bottom:20px}.signin-box .form fieldset label{font-size:80%;color:#297e77;width:100%}.signin-box .form fieldset .button{border:solid 2px #34aca6;border-radius:15px;background:linear-gradient(145deg, #25e4ab 0%, #34aca6 50%, #297e77 100%);color:#171818;padding:5px;box-shadow:0 1px 8px #171818;font-family:"Share Tech Mono";font-size:110%;width:100px}.signin-box .form fieldset .button:hover{cursor:pointer;color:#171818;box-shadow:inset 0 2px 10px #297e77;border-color:#297e77}

